<template>
  <div class="main">
    <div class="main-map">
      <yandex-map :coords="coords" :zoom="15" @click="onClick" :options="options" :controls="controls">
        <ymap-marker :coords="tasks[0].coords" marker-id="0" :balloon-template="balloonTemplate(tasks[0].name, tasks[0].desc, tasks[0].people, tasks[0].points)" />
      </yandex-map>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      coords: [ 54.817533, 58.448946 ],
      options: {
        restrictMapArea: [
            [54.83381208998252,58.4077226876815],
            [54.79925718380296,58.4894802010907]
        ]
      },
      controls: ['zoomControl'],
      tasks: [
        {
          id: 0,
          coords: [ 54.817533, 58.448946 ],
          name: 'Сдача макулатуры',
          desc: 'Приглашаем сдавать макулатуру',
          people: 1,
          points: 20
        }
      ]
    }
  },
  methods: {
    onClick (e) {
      this.coords = e.get('coords')
    },

    balloonTemplate(name, desc, people, points) {
      return `
      <h1>${name}</h1>
      <p>${desc}</p>
      <p>Атомы за выполнение: ${20}</p>

      `
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.main-map {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
 }

 .ymap-container {
    width: 100%;
    height: 100%;
 }
</style>



